.header {
  overflow: hidden;
  width: 100%;
  height: 43.125rem;

  @media (min-width: 640px) {
    height: 56.25rem;
    justify-content: center;
  }
}

.header-bg {
  width: 100%;
  height: 43.125rem;
  position: relative;

  @media (min-width: 640px) {
    height: 50rem;
  }
}

.header-img-bg {
  position: absolute;
  width: 100%;
  height: 43.125rem;
  object-fit: cover;
  object-position: top;

  @media (min-width: 640px) {
    height: 56.25rem;
    object-position: center;
  }
}

.nav-version1,
.nav-version2 {
  position: absolute;
  top: 0;
  color: #ffffff;
  display: flex;
  align-items: center;
  justify-content: space-between;
  width: 100%;
  height: 8.5rem;

  @media (min-width: 640px) {
    padding: 2rem 1.5rem 1.5rem 1.5rem;
  }
}

.special-navbar {
  position: fixed;
  top: 0;
  z-index: 3;
  background: linear-gradient(
    91.04deg,
    rgba(255, 255, 255, 0.1) 0%,
    rgba(255, 255, 255, 0.024) 100%
  );
  backdrop-filter: blur(10.5px);
}

.x.y {
  transition: 0.3s ease-in-out;
  color: black;
}

.shopping-bag {
  font-size: 2rem;
}

.header-logo-text {
  @media (max-width: 640px) {
    flex-direction: column;
    align-items: flex-start;
  }
  font-size: 1.875rem;
}

.nav-list {
  display: flex;
  align-items: center;

  @media (max-width: 640px) {
    flex-direction: column;
    align-items: flex-start;
  }
}

.nav-list-item {
  font-size: 1.15rem;
  margin-right: 3rem;
  font-weight: 500;

  @media (max-width: 640px) {
    margin-right: 0;
    margin-bottom: 1rem;
  }

  @media (min-width: 768px) {
    display: block;
  }

  display: none;
  &:hover {
    transform: scale(1.1);
    transition-duration: 0.2s;
  }
}

.shoppingbag {
  width: 2.75rem;

  &:hover {
    transform: scale(1.1);
    transition-duration: 0.2s;
  }
}

.account-icon {
  font-size: 1.9rem;
}

.hero-image {
  position: absolute;

  @media (min-width: 640px) {
    left: 2.5rem;
    top: 2.5rem;
    width: 43.75rem;
    align-items: center;
    justify-content: flex-start;
    padding-top: 5rem;
    padding-left: 1.5rem;
    right: 0;
  }

  top: 2.5rem;
  right: 15vw;
  width: 70vw;
  display: flex;
  justify-content: center;
  padding-top: 3.75rem;
  margin-left: auto;
  margin-right: auto;
}

.hero-text-con {
  color: #ffffff;
  display: flex;
  justify-content: center;
  flex-direction: column;
  max-width: 100vw;
  padding: 0;
  margin: 0;
  @media (min-width: 640px) {
    margin-left: -3.5rem;
  }
  // @media (min-width: 767px) {
  //   margin-left: 5rem;
  // }
  @media (min-width: 1025px) {
    margin-left: -20vw;
  }
}

.hero-title {
  text-align: left;
  margin-left: auto;
  margin-right: auto;
  margin-top: 10rem;
  font-size: 5rem;
  padding-top: 0;
  line-height: 120%;

  @media (max-width: 640px) {
    font-size: 2.5rem;
    padding-left: 0;
    text-align: center;
    margin-bottom: 2.5rem;
  }
  @media (min-width: 641px) {
    margin-left: 3.75rem;
  }
}

.hero-subtitle {
  text-align: center;

  // padding-left: 0;
  color: #ffffff;

  width: 15rem;
  font-size: 1rem;
  margin-top: 2.5rem;

  @media (min-width: 640px) {
    text-align: left;
    margin-left: 4.5rem;

    width: 25rem;
    font-size: 1.35rem;
  }

  @media (max-width: 640px) {
    // margin-left: 0rem;
    margin-left: auto;
    margin-right: auto;

    width: 15rem;
    font-size: 0.75rem;
    margin-top: 1rem;
  }
}

.hero-button-con {
  display: flex;

  @media (max-width: 640px) {
    padding: 0rem;
    margin: 0;
    margin-top: 4rem;

    justify-content: center;
  }

  justify-content: flex-start;
  margin-top: 2.5rem;
  margin-left: 4.5rem;
}

.hero-button {
  padding-left: 2rem;
  padding-right: 2rem;
  margin-bottom: 1.75rem;
  padding-top: 0.75rem;
  padding-bottom: 0.75rem;
  font-size: 1rem;
  font-weight: 600;
  background-color: #00796b;
  border-radius: 0.375rem;
  border-color: #ffffff;
  border-style: solid;
  border-width: 0.025rem;

  &:hover {
    background-color: #10b981;
  }

  box-shadow: 0 2px 6px rgba(0, 0, 0, 0.1);
}

.dropdown-container {
  position: relative;
}

.dropdown-content {
  display: flex;
  flex-direction: column;
  position: absolute;
  top: 100%;
  left: 0;
  background-color: #fff;
  border: 1px solid #ccc;
  border-radius: 0.25rem;
  box-shadow: 0 2px 6px rgba(0, 0, 0, 0.1);
  z-index: 1;

  button {
    padding: 0.75rem;
    text-align: left;
    width: 100%;
    border: none;
    background-color: #fff;
    color: #00796b;
    cursor: pointer;

    &:hover {
      background-color: #f2f2f2;
    }
  }
}
