.footer-container {
  background-color: #00796b;
  display: flex;
  justify-content: center;
  flex-direction: column;
  align-items: center;
  width: 100vw;
  height: 8.5rem;
  text-align: left;
  color: #ffffff;
  text-transform: uppercase;
  // position: absolute;
  // bottom: 100%;
  @media (min-width: 640px) {
    justify-content: space-between;
    flex-direction: row;
    align-items: flex-start;

    padding-left: 7rem;
    padding-right: 6rem;
    padding-top: 2rem;
  }
}

.logo-container {
  display: flex;
  margin-bottom: 6px;
}

.logo-text {
  font-size: 1.75rem;
  margin-bottom: 6px;
}

.social-container {
  display: flex;
  flex-direction: column;
}

.follow-text {
  margin-bottom: 0.5rem;
}

.social-icons {
  display: flex;
  justify-content: space-evenly;
}

.social-link {
  margin-right: 4px;
}

.social-icon {
  width: 2rem;
  filter: invert(100%);
  transition: transform 0.2s ease-in-out;

  &:hover {
    transform: scale(1.1);
  }
}
